<template>
 <div class="course-construction">
     <title-curmb></title-curmb>
     <course-steps :title="courseName" :step="6"></course-steps>
     <div class="course-construction_body">
         <el-form :model="form" :rules="rules" ref="form" label-width="0">
            <h2>材料及参考书目</h2>
            <el-row>
                 <el-form-item  prop="booklist">
                    <el-input v-model="form.booklist"  type="textarea" :rows="7" placeholder="请填写教材或参考书目名称、主编、出版社"></el-input>
                </el-form-item>   
            </el-row>
             <h2>备注说明</h2>
            <el-row>
                 <el-form-item  prop="explains">
                    <el-input v-model="form.explains"  type="textarea" :rows="7" placeholder="请将对本课程大纲的备注说明填写在这里..."></el-input>
                </el-form-item>   
            </el-row>
            <el-row :gutter="24">
                <el-col :span="6" :offset="9">
                     <div class="c-footer">
                        <el-button @click="formSubmit('form', 'save')">保&nbsp;&nbsp;存</el-button>
                        <el-button type="primary" @click="formSubmit('form', 'submit')">提&nbsp;&nbsp;交</el-button>
                    </div>
                </el-col>
            </el-row>
       </el-form>
     </div>
 </div>
</template>

<script>
import TitleCurmb from './components/TitleCurmb'
import CourseSteps from './components/CourseSteps'

export default {
 name: 'Reference',
 components: {TitleCurmb, CourseSteps},
 data() {
 return {
    courseName: this.$route.query.title,
    form:{
        booklist:'',
        explains:''
    },
    rules:{
        booklist: [{ required: true, message: "请输入材料及参考书目", trigger: "blur"}],  
        explains: [{ required: true, message: "请输入备注说明", trigger: "blur"}]
    },
    api:{
         details:courseId=>`/basic/course/setCourseBooklistView/${courseId}`, //详情
         save:() => `/basic/course/setCourseBooklist`
    }
 }
 },
 created(){},
 mounted(){
     this.initData()
 },
 methods:{
      initData(){
        const url=this.api.details(this.$route.query.id)
        this.$get(url).then(res =>{
            if(res.status ===1){    
                this.form=res.data;
                
            }else{
                this.$notify.error({
                    title: "error",
                    duration: 1500,
                    message: res.message
                });
            }
        })
     },
     formSubmit(formName, type){

         const postData=JSON.parse(JSON.stringify(this.form))
         postData.id=this.$route.query.id

         if(type === 'save'){
             this.$refs[formName].resetFields()
             this.userSave(postData, 'save')
         }else{ 
            this.$refs[formName].validate(valid => {
                if (valid) {
                   this.userSave(postData, 'next')
                }
            });
         }
       
     },
    userSave(postData, type){
        this.$post(this.api.save(), postData).then( res => {
          if(res.status === 1){
              this.$notify.success({
                  title: 'success',
                  duration:1500,
                  message: res.message
                  })
              if(type === 'next'){
                    this.$router.push({
                      path:'/Construction/previewCourse',
                      query:{
                          id:this.$route.query.id
                      }
                  })
              }else{
                  this.initData()
              } 
          }else{
              this.$notify.error({
                  title: 'error',
                  duration:1500,
                  message: res.message
              })
          }  
      })
     }
 }
}
</script>

<style lang='scss' scoped>
 @import '../../styles/courseConstruction.scss';
</style>



